import React from 'react';

import classNames from 'classnames';

import PropTypes from 'prop-types'; 

import { calcHLTV, secToDate } from '../../assets/js/utils';

function MatchItem({ match, eloDif, setMatchId, setShowMatches, search, history }) {
	const {
		elo,
		c2: kd,
		c3: kr,
		c4: hsInPercent,
		i5: teamName,
		i6: kills,
		i7: assists,
		i8: deaths,
		// i9: mvps,
		i10: isWin,
		i12: rounds,
		// i13: hsCount,
		i14: triple,
		i15: quadro,
		i16: penta,
	} = match
	const bgClass = classNames({
		match__item: true,
		'match__win': isWin === '1',
		'match__lose': isWin === '0'
	});
	const hltvRating = calcHLTV({ kills, rounds, deaths, triple, quadro, penta });

	const hltvClass = classNames({
		'kd-high': Number.parseFloat(hltvRating) > 1,
		'kd-low': Number.parseFloat(hltvRating) < 1,
		'kd-one': Number.parseFloat(hltvRating) === 1
	});

	const teamString = teamName.includes('team_') ? teamName.slice(5) : teamName;

	const teamClass = classNames({
		'team-long': teamString.length > 15,
	});

	const matchElo = elo ? elo : '+0';
	const eloSign = eloDif > 0 ? `+${eloDif}` : eloDif;
	const eloDiffNaN = Number.isNaN(eloDif) ? '+0' : (eloSign);
	const eloDiff =  eloDif ? eloDiffNaN : '';
	const eloClass = classNames({
		'kd-high': eloDif > 0,
		'kd-low': eloDif < 0,
	});

	const onItemClick = () => {
		setMatchId(match.matchId);
		setShowMatches(false);
		history.push({
			pathname: `/match/${match.matchId}`,
			search
		});
	};

	return (
		<div onClick={onItemClick}
			className={bgClass}
		>
			<span>{secToDate(match.created_at)}</span>
			<div className="match__elo">
				{matchElo}&nbsp;
				<span className={eloClass}>{eloDiff}</span>
			</div>
			<span>{match.i18.split('/').join('-')}</span>
			<span>{kr}</span>
			<span>{kd}</span>
			<span>{hsInPercent}%</span>
			<span>{kills}</span>
			<span>{assists}</span>
			<span>{deaths}</span>
			<span>{match.i1.split('/').pop()}</span>
			<span className={teamClass}>{teamString}</span>
			<span className={hltvClass}>{hltvRating}</span>
		</div>
	);
}

MatchItem.propTypes = {
	match: PropTypes.object,
	eloDif: PropTypes.number,
	setMatchId: PropTypes.func,
	setShowMatches: PropTypes.func,
	search: PropTypes.string,
}

export default MatchItem;
